import { login_success, login_failed, load_user_failed, load_user_success, auth_suc, auth_faild } from './features/userSlice'
import axios from 'axios'
import { createAsyncThunk } from '@reduxjs/toolkit';


const login = async (data, dispach) => {

    const url = "http://127.0.0.1:7000/auth/";
    const config = { headers: { "Content-Type": "application/json" } }
    const body = JSON.stringify(data);
  
  
    try {
      const res = await axios.post(`${url}jwt/create/`, data, config)
      console.log(res.data)
      dispach(login_success(res.data))
      dispach(loadUser(dispach))
      
  
    } catch (err) {
      console.log(err)
      
     
      
  
    }
  
  }
  
  
  const loadUser = async (dispach) => {
    if (localStorage.getItem("access")) {
  
  
      const url = "http://127.0.0.1:7000/auth/";
      const config = {
        headers: {
          "Content-Type": "application/json",
          "Authorization": "JWT " + localStorage.getItem("access"), 
          "Accept" : "application/json"
        }
      }
  
      try{
       const res = await axios.get(`${url}users/me/`,config)
       console.log(res.data)
       console.log(config)
       dispach(load_user_success(res.data))
  
      }catch(err){
        console.log(err)
       // dispach(load_user_failed())
      }
  
    }else(
      dispach(load_user_failed())
    )
  
  }


  const checkAuthenticated = async(dispach)=>{
    if (localStorage.getItem("access")) {

        
      const url = "http://127.0.0.1:7000/auth/";
      const config = {
        headers: {
          "Content-Type": "application/json",
          "Accept" : "application/json"
        }
      }
      const body = JSON.stringify({token:localStorage.getItem("access")})


      // axios
        try{
            const res = await axios.post(`${url}jwt/verify/`,body,config)
            
            if (res.data.code !== "token_not_valid") {
                dispach(auth_suc())
                console.log("zes")
            }else{
                dispach(auth_faild())
            }
            console.log(res.data)

        }catch(err){
            console.log("requset faild")
            console.log(err)
           // dispach(auth_faild())
        }
      
  



    }
  
  }

  const logout = (dispach)=>{
    dispach(login_failed)
  }



  
  export {loadUser,login,logout,checkAuthenticated}
