import { createBrowserRouter, createRoutesFromElements, Route, RouterProvider } from 'react-router-dom'
import Home from './pages/home';
import Login from './pages/login';
import Root from './layouts/root';
import SignUp from './pages/signUp';
import Activate from './pages/Activate';
import RestPass from './pages/RestPass';
import RestPassConfirm from './pages/RestPassConfirm';
import MiniDrawer from './layouts/sidebar';
import { companyLouder } from './pages/home';
import Company from './pages/Company';
import { detailLoader } from './pages/Company';

function App() {
  const router = createBrowserRouter(
    createRoutesFromElements(
      <Route>
        <Route path='/' element={<MiniDrawer />}>
          <Route index element={<Home />} loader={companyLouder}/>
          <Route path='company/:id' element={<Company/>} loader={detailLoader}/>
          


        </Route>
        <Route path='auth' element={<Root/>}>
          <Route path='login' element={<Login />} />
          <Route path='signup' element={<SignUp />} />
          <Route path='activate/:uid/:token' element={<Activate />} />
          <Route path='restpass' element={<RestPass />} />
          <Route path='password/reset/confirm/:uid/:token' element={<RestPassConfirm  />} />
        </Route>

      </Route>

    )

  )
  return (
    <div>
      <RouterProvider router={router} />
    </div>

  );
}

export default App;
