import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Navigate, redirect } from 'react-router-dom'
import { login } from '../features/userSlice'
import { logout } from '../actions'

export default function Login() {


  const dispach = useDispatch()

  // state data
  const [formData,setFormData] = useState({})
  // onChange
  const Change = (e)=>{
    setFormData({...formData, [e.target.name]:e.target.value})
    console.log(formData)
  }

  // submit logic

  const submit = (e) => {
    e.preventDefault()
    console.log("working")
  
    dispach(login({email:"mahmod02015@gmail.com",password:"qwertzuiop+"}))

  }

  // if user authentcited
  const is_auth = useSelector(state=>state.user.isAuthenticated)
  if (is_auth){
    return (<Navigate to="/"/>)
  }


  return (
    <div>
      <form onSubmit={(e) => submit(e)}>
        <div class="container">
          <label>Username : </label>
          <input onChange={Change} type="text" placeholder="Enter Username" name="email" defaultValue={formData.email} required />
          <label>Password : </label>
          <input onChange={Change} type="password" placeholder="Enter Password" defaultValue={formData.password} name="password" required />
          <button type="submit" >Login</button>
          <input type="checkbox" checked="checked" /> Remember me
          Forgot <a href="#"> password? </a>
        </div>
      </form>
      <button onClick={()=>logout(dispach)} type="button" class="cancelbtn"> logout</button>

    </div>
  )
}

